import fetch from './fetch/index';

/**
 * ajax
 * @param {any}
 */
class UserService {
  /**
   * 
   * @param {username} 
   * @param {password} 
   * @return {}
   */
  goLogin = (params) => fetch('POST', '/user/login', params);

}

// 
export default new UserService();



